body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  width: 100%;
  height: 100%;
}

.main {
  width: 100%;
  height: 100%;
}

table {
  width: 550px;
  height: 550px;
  display: table;
}

header{
  font-size: 30px;
  color:rgb(3, 5, 117);

  text-shadow: 1px 1px 1px rgb(50, 66, 80);
}

td,
tr {
  border: 1px solid rgb(3, 5, 117);
}
td {
  box-shadow: 0.5px 0.5px 1px rgb(6, 27, 41);
  background: rgb(231, 236, 247);

  border-radius: 10%;
}

.colored {
  background: rgb(215, 217, 248);
}

.game-btn {
  width: 100px;
  height: 50px;
  font-size: 15px;
  font-weight: bold;
  line-height: 1.1;

  color: rgb(3, 5, 117);
  background: rgb(231, 236, 247);

  border-radius: 10px;
}

.btn-container {
  width: 30%;
  height: 20%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.game-btn:focus {
  outline: none;
}

.game-btn:hover {
  background: rgb(3, 5, 117);
  color: rgb(231, 236, 247);
}

.main-container {
  width: 100%;
  height: 100%;

  background: rgb(242, 244, 248);

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

